// src/pages/Login.js
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { auth } from "../firebase";
import { signInWithEmailAndPassword, signInWithPopup, GoogleAuthProvider } from "firebase/auth";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const handleEmailLogin = async () => {
    try {
      await signInWithEmailAndPassword(auth, email, password);
      navigate("/chat");
    } catch (error) {
      console.error("Login error:", error);
    }
  };

  const handleGoogleLogin = async () => {
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider);
      navigate("/chat");
    } catch (error) {
      console.error("Google login error:", error);
    }
  };

  return (
    <div>
      <h2>ng nhp</h2>
      <input type="email" placeholder="Email" onChange={(e) => setEmail(e.target.value)} />
      <input type="password" placeholder="Mt khu" onChange={(e) => setPassword(e.target.value)} />
      <button onClick={handleEmailLogin}>ng nhp bng Email</button>
      <button onClick={handleGoogleLogin}>ng nhp bng Google</button>
      <p>Bn cha c ti khon? <a href="/signup">ng k ngay</a></p>
    </div>
  );
};

export default Login;
